@font-face {
    font-family: myFont;
    src: url(FC_Home.ttf);
}

* {
    font-family: myFont;
}

html {
    scroll-behavior: smooth;
}

html::-webkit-scrollbar {
    width: 0px;
    background: transparent;
}

header,
body {
    padding: 0;
    margin: 0;
    background-color: #5FC9A3;
}

body {
    background-image: url("img/background.png");
    background-position: top;
    background-attachment: fixed;
}

#top-block {
    background-color: #3D9D82;
    position: sticky;
    top: 0;
}

#top-button-home {
    display: inline-block;
    color: #F4D429;
    text-decoration: none;
    text-align: center;
    font-weight: bold;
    padding: 1.5%;
    background-color: #483873;
    border-left: 2px solid #F4D429;
    border-right: 2px solid #F4D429;
    width: 5%;
}

#top-button-other {
    display: inline-block;
    color: #ffffff;
    text-decoration: none;
    text-align: center;
    padding: 1.5%;
    float: right;
    width: 5%;
}

#top-button-select {
    display: inline-block;
    color: #F4D429;
    text-decoration: none;
    text-align: center;
    padding: 1.5%;
    float: right;
    background-color: #38365B;
    border-left: 1px solid#F4D429;
    border-right: 1px solid #F4D429;
    width: 5%;
}

#top-time {
    display: inline-block;
    width: 15%;
    text-decoration: none;
    text-align: center;
}

#top-button-other:hover {
    background-color: #685D88;
    color: #F4D429;
}

#home-back {
    width: 100%;
    position: absolute;
    top: 0;
}

#home1 {
    width: 100%;
    position: fixed;
    animation: rightmove 0.4s linear infinite;
    animation-play-state: paused;
    animation-delay: calc(var(--scroll) * -1s);
    animation-iteration-count: 1;
    animation-fill-mode: both;
}

@keyframes rightmove {
    from {
        left: 0%;
        opacity: 100%;
    }
    to {
        left: 100%;
        opacity: 0;
    }
}

#home2 {
    width: 100%;
    position: fixed;
    animation: leftmove 0.4s linear infinite;
    animation-play-state: paused;
    animation-delay: calc(var(--scroll) * -1s);
    animation-iteration-count: 1;
    animation-fill-mode: both;
}

@keyframes leftmove {
    from {
        right: 0%;
        opacity: 100%;
    }
    to {
        right: 100%;
        opacity: 0;
    }
}

#home-text {
    width: 100%;
    display: block;
    margin: auto;
    text-align: center;
}

#start {
    width: 10%;
    display: block;
    margin: auto;
    transition: transform .2s;
}

#start:hover {
    transform: scale(1.2);
}

#home3 {
    margin-top: 10%;
    padding-top: 6%;
    width: 100%;
}

#data-zone {
    max-width: 80%;
    margin: auto;
    padding: 0 2% 0 2%;
    text-align: center;
}

#data-button {
    display: inline-block;
    max-width: 20%;
    margin-left: 0.5%;
    margin-right: 0.5%;
    background-color: #685D88;
    text-align: center;
    padding: 1.5%;
    color: white;
}

#data-button:hover {
    background-color: #38365B;
    color: #F4D429;
}

#data-button-img {
    display: inline-block;
    max-width: 20%;
    vertical-align: middle;
    margin-left: 5%;
    margin-right: 5%;
}

#data-button-text {
    display: inline-block;
    text-align: center;
    margin-left: 5%;
    margin-right: 5%;
}

#home-link {
    width: 90%;
    display: block;
    margin: auto;
    margin-bottom: 4%;
    padding: 3% 0 3% 0;
    background-size: cover;
    text-align: center;
    background-position: center;
    font-size: 100px;
    color: #F4D429;
}

#home-link:hover {
    padding: 2% 0 2% 0;
    font-size: 134px;
    transition: font-size 0.3s ease-out, padding 0.3s ease-out;
    color: #F4D429;
    box-shadow: 0 5px 20px white;
}

table {
    display: table;
    margin: auto;
    width: 80%;
    border-collapse: collapse;
    font-size: 30px;
    background-color: #685D88;
}

#td1 {
    width: 30%;
    text-align: right;
    padding-right: 1%;
    color: #F4D429;
}

#td2 {
    width: 70%;
    text-align: left;
    padding-left: 1%;
    color: white;
}

.lang-button {
    display: inline-block;
    width: 40%;
    padding: 1.5%;
    text-align: center;
    color: #F4D429;
    border: none;
    outline: none;
    font-size: 30px;
}